<template>
    <div class="sworks">
        <div class="crumbs">
            <el-breadcrumb separator="/">
                <el-breadcrumb-item>
                    <i class="el-icon-lx-calendar"></i> 教师管理
                </el-breadcrumb-item>
            </el-breadcrumb>
        </div>
        <div class="container">
            <div class="form">
                <div class="form-top">
                    <el-button class="fr downloadbutton" type="primary" size="small">下载用户模板</el-button>
                    <el-button class="fr" type="primary" size="small">导入教师用户</el-button>
                    <el-button class="fr" type="primary" @click="handleEdit" size="small">创建教师用户</el-button>
                </div>
                <div class="cont">
                    <el-table
                      :data="tableData"
                      style="width: 100%;margin-top:20px;">
                      <el-table-column
                        type="index"
                        width="100px"
                        label="序号">
                      </el-table-column>
                      <el-table-column
                        prop="date"
                        label="学号">
                      </el-table-column>
                      <el-table-column
                        prop="address"
                        label="姓名">
                      </el-table-column>
                      <el-table-column
                        prop="address"
                        label="院系">
                      </el-table-column>
                      <el-table-column label="操作" width='240px'>
                          <template slot-scope="scope">
                            <el-button
                              size="mini"
                              @click="handleEdit">编辑</el-button>
                            <el-button
                              size="mini"
                              type="info"
                              @click="resetpassword">重置密码</el-button>
                            <el-button
                              size="mini"
                              type="danger"
                              @click="delworks">删除</el-button>
                          </template>
                        </el-table-column>
                    </el-table>

                    <div class="pagination">
                        <el-pagination
                          background
                          layout="prev, pager, next"
                          :total="1000">
                        </el-pagination>
                    </div>
                    
                </div>
            </div>
        </div>
        <!-- 编辑弹出框 -->
        <el-dialog title="创建老师" class="datas-bg" :visible.sync="editVisible" width="380px">
            <el-input class="margin10" v-model="input" size="small" placeholder="请输入学号"></el-input>
            <el-input class="margin10" v-model="input" size="small" placeholder="请输入姓名"></el-input>
            <el-select class="margin10" v-model="value" size="small" placeholder="请选择入学年份">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select><br />
            <el-select class="margin10" v-model="value" size="small" placeholder="请选择院系">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select><br />
            <el-select class="margin10" v-model="value" size="small" placeholder="请选择班级">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select><br />
            <el-button type="primary" style="display:block;margin:0 auto;" size="small">确 定</el-button>
        </el-dialog>
    </div>
</template>

<script>
export default {
    name: 'course',
    data() {
        return {
            editVisible:false,
            choosed:'未完成课程设计教学',
            items:[1,1,1],
            tableData: [{
                date: '2016-05-02',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1518 弄'
              }, {
                date: '2016-05-04',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1517 弄'
              }, {
                date: '2016-05-01',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1519 弄'
              }, {
                date: '2016-05-03',
                name: '王小虎',
                address: '上海市普陀区金沙江路 1516 弄'
              }]
        };
    },
    methods: {
        openLink(){
            this.$router.push({path:'newworks'})
        },
        handleEdit(){
            this.editVisible=true;
        },
        resetpassword(){
          this.$confirm('确定要重置密码吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.$message.success('重置成功');
                this.tableData.splice(index, 1);
            })
            .catch(() => {});
        },
        delworks(){
            this.$confirm('确定要删除此教师吗？', '提示', {
                type: 'warning'
            })
            .then(() => {
                this.$message.success('删除成功');
                this.tableData.splice(index, 1);
            })
            .catch(() => {});
        }
    }
};
</script>
<style lang='scss'>
    .datas-bg{
         .el-dialog__body{
            padding:10px 20px 30px;
         }
    }
</style>
<style lang='scss' scoped>
    .sworks{
        .form-top{
            overflow:hidden;
        }
    }
    .pagination{
        margin-top:20px;
    }
    .downloadbutton{
      margin-left:10px;
    }
    .margin10{
      margin-bottom:10px;
      width:100%;
    }
</style>


